import { useState } from "react";
import { Card, CardContent, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { 
  Upload, 
  CheckCircle, 
  Loader2, 
  Download, 
  RotateCw, 
  AlertCircle,
  Info,
  Book,
  PieChart,
  List,
  FileText,
  ChevronDown,
  ChevronUp,
  HelpCircle
} from "lucide-react";
import { 
  Collapsible, 
  CollapsibleContent, 
  CollapsibleTrigger 
} from "@/components/ui/collapsible";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { PieChart as RechartsChart, Pie, Cell, ResponsiveContainer, Legend, Tooltip as ChartTooltip } from "recharts";
import { Progress } from "@/components/ui/progress";
import { Badge } from "@/components/ui/badge";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";
import { Step, Stepper } from "@/components/ui/stepper";

const DashboardPage = () => {
  // State management
  const [file, setFile] = useState(null);
  const [isDragging, setIsDragging] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [reportGenerated, setReportGenerated] = useState(false);
  const [reportContent, setReportContent] = useState("");
  const [backendResponse, setBackendResponse] = useState(null);
  const [error, setError] = useState(null);
  const [activeTab, setActiveTab] = useState("outcomes");
  const [showHelp, setShowHelp] = useState(false);
  const [currentStep, setCurrentStep] = useState(1);
  const [showTooltips, setShowTooltips] = useState(true);

  // Handle file selection
  const handleFileChange = (event) => {
    const selectedFile = event.target.files?.[0];
    if (
      selectedFile &&
      (selectedFile.type === "application/pdf" ||
        selectedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        selectedFile.type === "application/msword")
    ) {
      setFile(selectedFile);
      setError(null);
      setCurrentStep(2);
    } else {
      setError("Please upload a valid PDF or DOC file.");
      setFile(null);
    }
  };

  // Drag and drop handlers
  const handleDragOver = (event) => {
    event.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = (event) => {
    event.preventDefault();
    setIsDragging(false);
    const droppedFile = event.dataTransfer.files?.[0];
    if (
      droppedFile &&
      (droppedFile.type === "application/pdf" ||
        droppedFile.type === "application/vnd.openxmlformats-officedocument.wordprocessingml.document" ||
        droppedFile.type === "application/msword")
    ) {
      setFile(droppedFile);
      setError(null);
      setCurrentStep(2);
    } else {
      setError("Please drop a valid PDF or DOC file.");
      setFile(null);
    }
  };

  // Score chart data
  const generateScoreData = (scoring) => {
    if (!scoring) return [];
    
    return [
      { name: "Bloom Coverage", value: scoring.bloom_coverage || 0, color: "#22c55e" },
      { name: "Alignment", value: scoring.alignment_score || 0, color: "#3b82f6" }, 
      { name: "Completeness", value: scoring.completeness_score || 0, color: "#ec4899" }
    ];
  };

  // Bloom taxonomy bar data
  const getBloomLevelColor = (level) => {
    const colors = {
      "Remember": "#d4b4ff", 
      "Understand": "#93c5fd",
      "Apply": "#86efac",
      "Analyze": "#fde047",
      "Evaluate": "#fdba74",
      "Create": "#f87171"
    };
    return colors[level] || "#cbd5e1";
  };

  const generatePdfReport = async (data) => {
    // We'll keep your existing PDF generation logic but improve it
    // This is a placeholder for the actual PDF generation 
    // which should be enhanced with charts, better formatting, etc.
    const fileName = file?.name
      ? `syllabus_improvement_${file.name.replace(/\.[^/.]+$/, "")}.pdf`
      : "syllabus_improvement_report.pdf";
      
    // PDF generation logic would go here
    // ...
    
    // For now, just set the report content
    const { syllabus, enhancement } = data;
    const originalOutcomes = syllabus?.content?.objectives || [];
    const improvedOutcomes = enhancement?.improved_outcomes || [];
    const misalignments = enhancement?.misalignments || [];
    const suggestions = enhancement?.suggestions || [];
    
    const content = `
Syllabus Improvement Report

File Processed: ${fileName}

Original Learning Outcomes:
${originalOutcomes.length === 0 ? "No objectives provided in the syllabus." : originalOutcomes.map((item) => `- ${item}`).join("\n")}

Improved Learning Outcomes:
${improvedOutcomes.length === 0 ? "No improved outcomes generated." : improvedOutcomes.map((item) => `- ${item}`).join("\n")}

Assessment Improvements:
Bloom's Taxonomy Analysis
- Found Levels: ${enhancement?.bloom_analysis?.found_levels?.join(", ") || "N/A"}
- Missing Levels: ${enhancement?.bloom_analysis?.missing_levels?.join(", ") || "N/A"}

Misalignments:
${misalignments.length === 0 ? "No misalignments detected." : misalignments.map((item) => `- ${item}`).join("\n")}

Suggestions:
${suggestions.length === 0 ? "No suggestions provided." : suggestions.map((item) => `- ${item}`).join("\n")}

Scoring:
- Score: ${enhancement?.scoring?.score || "N/A"}/100
- Bloom Coverage: ${enhancement?.scoring?.bloom_coverage || "N/A"}%
- Alignment Score: ${enhancement?.scoring?.alignment_score || "N/A"}
- Completeness Score: ${enhancement?.scoring?.completeness_score || "N/A"}
    `;

    setReportContent(content.trim());
    return true;
  };

  // Form submission handler
  const handleSubmit = async (event) => {
    event.preventDefault();
    if (!file) {
      setError("Please select a file to upload.");
      return;
    }

    setIsLoading(true);
    setError(null);
    setCurrentStep(3);

    try {
      const formData = new FormData();
      formData.append("file", file);

      const response = await fetch("http://localhost:5000/api/syllabus/upload", {
        method: "POST",
        body: formData,
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const data = await response.json();
      if (data.error) {
        if (data.error.includes("GROQ_API_KEY") || data.error.includes("Groq client")) {
          setError("The server is misconfigured. Please contact the administrator.");
        } else if (data.error.includes("No text")) {
          setError("The uploaded file contains no readable text. Please upload a valid syllabus file.");
        } else {
          setError(data.error || "An error occurred while processing the file.");
        }
        throw new Error(data.error);
      }

      setBackendResponse(data);
      await generatePdfReport(data);
      setReportGenerated(true);
      setCurrentStep(4);
    } catch (error) {
      console.error("Error during file upload:", error);
      setError("Failed to process the file. Please try again.");
      setCurrentStep(2);
    } finally {
      setIsLoading(false);
    }
  };

  // Download handler
  const handleDownload = async () => {
    if (!backendResponse) {
      setError("No report data available to download.");
      return;
    }

    try {
      await generatePdfReport(backendResponse);
      // In a real implementation, this would generate and download the PDF
      alert("PDF download functionality will be implemented with jsPDF");
    } catch (error) {
      setError("Failed to download report. Please try again.");
      console.error(error);
    }
  };

  // Reset handler
  const handleReset = () => {
    if (window.confirm("Are you sure you want to reset? All current data will be lost.")) {
      setFile(null);
      setReportGenerated(false);
      setReportContent("");
      setBackendResponse(null);
      setError(null);
      setCurrentStep(1);
      setActiveTab("outcomes");
    }
  };

  // Help content component
  const HelpGuide = () => (
    <Card className="mt-4 border border-blue-200 bg-blue-50 dark:bg-blue-900/20 dark:border-blue-800">
      <CardHeader className="pb-2">
        <div className="flex items-center justify-between">
          <h3 className="text-lg font-semibold text-blue-700 dark:text-blue-300 flex items-center">
            <HelpCircle className="w-5 h-5 mr-2" />
            How to Use This Tool
          </h3>
          <Button 
            variant="ghost" 
            size="sm" 
            onClick={() => setShowHelp(false)}
            className="text-blue-700 dark:text-blue-300 hover:bg-blue-200 dark:hover:bg-blue-800/50"
          >
            Close
          </Button>
        </div>
      </CardHeader>
      <CardContent>
        <div className="space-y-4 text-sm text-blue-700 dark:text-blue-200">
          <div>
            <h4 className="font-semibold mb-1">1. Upload Your Syllabus</h4>
            <p>Upload your syllabus document in PDF or DOC/DOCX format. You can drag and drop or browse your files.</p>
          </div>
          <div>
            <h4 className="font-semibold mb-1">2. Analyze Your Syllabus</h4>
            <p>Our AI will analyze your learning outcomes against Bloom's Taxonomy and best practices.</p>
          </div>
          <div>
            <h4 className="font-semibold mb-1">3. Review Improvements</h4>
            <p>Review suggested improvements for learning outcomes, check alignment issues, and view your syllabus score.</p>
          </div>
          <div>
            <h4 className="font-semibold mb-1">4. Download Report</h4>
            <p>Download a comprehensive PDF report with all analysis and suggestions for later reference.</p>
          </div>
          <div className="pt-2">
            <p className="font-semibold">Understanding Bloom's Taxonomy:</p>
            <ul className="list-disc list-inside ml-2 space-y-1 mt-1">
              <li><span className="font-medium">Remember:</span> Recall facts and basic concepts</li>
              <li><span className="font-medium">Understand:</span> Explain ideas or concepts</li>
              <li><span className="font-medium">Apply:</span> Use information in new situations</li>
              <li><span className="font-medium">Analyze:</span> Draw connections among ideas</li>
              <li><span className="font-medium">Evaluate:</span> Justify a stand or decision</li>
              <li><span className="font-medium">Create:</span> Produce new or original work</li>
            </ul>
          </div>
        </div>
      </CardContent>
    </Card>
  );

  // Process status stepper
  const ProcessStepper = () => (
    <div className="w-full mb-8">
      <Stepper activeStep={currentStep} orientation="horizontal">
        <Step 
          completed={currentStep > 1}
          active={currentStep === 1}
          icon={<Upload className="w-5 h-5" />}
          label="Upload Syllabus"
        />
        <Step
          completed={currentStep > 2}
          active={currentStep === 2}
          icon={<FileText className="w-5 h-5" />}
          label="Review File"
        />
        <Step
          completed={currentStep > 3}
          active={currentStep === 3}
          icon={<PieChart className="w-5 h-5" />}
          label="Analyzing"
        />
        <Step
          completed={currentStep === 4}
          active={currentStep === 4}
          icon={<CheckCircle className="w-5 h-5" />}
          label="Results Ready"
        />
      </Stepper>
    </div>
  );

  // Score gauge visualization
  const ScoreGauge = ({ score = 0 }) => {
    const scoreColor = 
      score >= 80 ? "text-green-500" :
      score >= 60 ? "text-yellow-500" :
      "text-red-500";
      
    return (
      <div className="flex flex-col items-center">
        <div className="relative h-32 w-32">
          <svg className="w-full h-full" viewBox="0 0 100 100">
            {/* Background circle */}
            <circle
              cx="50"
              cy="50"
              r="45"
              fill="none"
              stroke="#e5e7eb"
              strokeWidth="10"
              strokeLinecap="round"
            />
            {/* Score circle */}
            <circle
              cx="50"
              cy="50"
              r="45"
              fill="none"
              stroke={score >= 80 ? "#22c55e" : score >= 60 ? "#eab308" : "#ef4444"}
              strokeWidth="10"
              strokeLinecap="round"
              strokeDasharray={`${(score/100) * 283} 283`}
              transform="rotate(-90 50 50)"
            />
            <text
              x="50"
              y="50"
              textAnchor="middle"
              dominantBaseline="central"
              className={`text-2xl font-bold ${scoreColor}`}
              fill="currentColor"
            >
              {score}
            </text>
            <text
              x="50"
              y="65"
              textAnchor="middle"
              className="text-xs font-medium text-gray-500"
              fill="currentColor"
            >
              out of 100
            </text>
          </svg>
        </div>
      </div>
    );
  };

  // BloomTaxonomy breakdown visualization  
  const BloomTaxonomyBreakdown = ({ bloomAnalysis }) => {
    if (!bloomAnalysis) return null;
    
    const allLevels = ["Remember", "Understand", "Apply", "Analyze", "Evaluate", "Create"];
    const foundLevels = bloomAnalysis.found_levels || [];
    const missingLevels = bloomAnalysis.missing_levels || [];
    
    return (
      <div className="space-y-3 mt-4">
        <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300">Bloom's Taxonomy Coverage:</h4>
        <div className="grid grid-cols-6 gap-2">
          {allLevels.map(level => {
            const isFound = foundLevels.includes(level);
            return (
              <div 
                key={level}
                className={`flex flex-col items-center p-2 rounded-lg ${
                  isFound 
                    ? 'bg-green-100 dark:bg-green-900/30 border border-green-200 dark:border-green-800' 
                    : 'bg-gray-100 dark:bg-gray-800 border border-gray-200 dark:border-gray-700 opacity-60'
                }`}
              >
                <div 
                  className="w-8 h-8 rounded-full flex items-center justify-center mb-1"
                  style={{ backgroundColor: getBloomLevelColor(level) }}
                >
                  {isFound ? (
                    <CheckCircle className="w-4 h-4 text-green-700 dark:text-green-300" />
                  ) : (
                    <AlertCircle className="w-4 h-4 text-gray-500 dark:text-gray-400" />
                  )}
                </div>
                <span className="text-xs font-medium text-center">
                  {level}
                </span>
              </div>
            );
          })}
        </div>
        
        {missingLevels.length > 0 && (
          <Alert className="bg-yellow-50 border-yellow-200 dark:bg-yellow-900/20 dark:border-yellow-800">
            <AlertCircle className="h-4 w-4 text-yellow-600 dark:text-yellow-400" />
            <AlertTitle className="text-yellow-800 dark:text-yellow-300">Missing Bloom Levels</AlertTitle>
            <AlertDescription className="text-yellow-700 dark:text-yellow-200 text-sm">
              Your syllabus is missing the following cognitive levels: 
              <span className="font-medium ml-1">{missingLevels.join(", ")}</span>. 
              Consider adding learning outcomes that address these levels.
            </AlertDescription>
          </Alert>
        )}
      </div>
    );
  };

  return (
    <div className="relative min-h-screen flex flex-col items-center justify-center overflow-hidden bg-gradient-to-br from-[#3b0000] via-[#4d0000] to-[#4e0000] px-4 py-8">
      <div className="relative z-10 w-full max-w-4xl flex flex-col items-center pt-2 pb-10 space-y-8">
        <div className="text-center">
          <h1 className="text-5xl font-extrabold leading-tight text-white drop-shadow-lg mb-2">
            <span className="text-white">Syllabus</span> <span className="text-red-300">Improvement</span>
          </h1>
          <p className="text-lg text-red-100 max-w-2xl">
            Enhance your course syllabus with AI-powered analysis and recommendations
          </p>
        </div>

        {!reportGenerated && <ProcessStepper />}

        <Card className="w-full bg-white/90 dark:bg-slate-900/90 backdrop-blur-md border border-gray-300 dark:border-gray-700 shadow-xl rounded-2xl">
          <CardHeader className="pb-2 flex flex-row items-center justify-between">
            <div>
              <h2 className="text-xl font-bold text-gray-800 dark:text-gray-200">
                {reportGenerated ? "Syllabus Analysis Results" : "Upload Your Syllabus"}
              </h2>
              <p className="text-gray-600 dark:text-gray-400 text-sm">
                {reportGenerated 
                  ? "Review your syllabus analysis and suggested improvements" 
                  : "Upload your syllabus in PDF or DOC format for analysis"
                }
              </p>
            </div>
            
            <TooltipProvider>
              <Tooltip>
                <TooltipTrigger asChild>
                  <Button 
                    variant="ghost" 
                    size="icon"
                    onClick={() => setShowHelp(!showHelp)}
                    className="rounded-full"
                  >
                    <HelpCircle className="h-5 w-5 text-gray-500 dark:text-gray-400" />
                  </Button>
                </TooltipTrigger>
                <TooltipContent>
                  <p>Get help using this tool</p>
                </TooltipContent>
              </Tooltip>
            </TooltipProvider>
          </CardHeader>

          <CardContent className="p-6">
            {showHelp && <HelpGuide />}
            
            {error && (
              <Alert className="mb-6 bg-red-50 border-red-200 dark:bg-red-900/20 dark:border-red-800">
                <AlertCircle className="h-4 w-4 text-red-600 dark:text-red-400" />
                <AlertTitle className="text-red-800 dark:text-red-300">Error</AlertTitle>
                <AlertDescription className="text-red-700 dark:text-red-200">
                  {error}
                </AlertDescription>
              </Alert>
            )}

            {!reportGenerated ? (
              <form onSubmit={handleSubmit} className="space-y-8">
                <div
                  className={`flex flex-col items-center justify-center w-full h-72 border-2 border-dashed rounded-xl cursor-pointer transition-colors duration-200 ${
                    isDragging
                      ? "border-red-400 bg-red-100 dark:bg-red-900/30"
                      : "border-gray-300 dark:border-gray-600 hover:bg-gray-100 dark:hover:bg-slate-800"
                  }`}
                  onDragOver={handleDragOver}
                  onDragLeave={handleDragLeave}
                  onDrop={handleDrop}
                >
                  <label
                    htmlFor="file-upload"
                    className="flex flex-col items-center justify-center w-full h-full"
                    aria-label="Upload syllabus file"
                  >
                    <div className="flex flex-col items-center space-y-4">
                      {file ? (
                        <div className="flex flex-col items-center">
                          <CheckCircle className="w-16 h-16 text-green-500 mb-2" />
                          <Badge className="bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200 mb-2">
                            File Selected
                          </Badge>
                        </div>
                      ) : (
                        <div 
                          className="w-20 h-20 rounded-full bg-red-100 dark:bg-red-900/30 flex items-center justify-center"
                        >
                          <Upload className="w-10 h-10 text-red-500" />
                        </div>
                      )}
                      <p className="text-lg font-medium text-gray-700 dark:text-gray-300">
                        {file ? file.name : "Upload PDF or DOC file"}
                      </p>
                      <p className="text-sm text-gray-500 dark:text-gray-400 flex items-center">
                        <Info className="w-4 h-4 mr-1" />
                        Click to browse or drag and drop
                      </p>
                    </div>
                    <input
                      id="file-upload"
                      type="file"
                      accept=".pdf,.doc,.docx"
                      onChange={handleFileChange}
                      className="hidden"
                      aria-describedby="file-upload-help"
                    />
                    <span id="file-upload-help" className="sr-only">
                      Upload a PDF or DOC file containing your syllabus
                    </span>
                  </label>
                </div>

                {file && (
                  <div className="bg-green-50 dark:bg-green-900/20 p-4 rounded-lg border border-green-200 dark:border-green-800">
                    <div className="flex items-start">
                      <div className="flex-shrink-0">
                        <CheckCircle className="h-5 w-5 text-green-500" />
                      </div>
                      <div className="ml-3">
                        <h3 className="text-sm font-medium text-green-800 dark:text-green-200">File ready for analysis</h3>
                        <div className="mt-2 text-sm text-green-700 dark:text-green-300">
                          <p>Click "Analyze Syllabus" to begin the AI-powered analysis of your document.</p>
                        </div>
                      </div>
                    </div>
                  </div>
                )}

                <Button
                  type="submit"
                  disabled={!file || isLoading}
                  className="w-full bg-gradient-to-r from-red-700 to-red-500 hover:from-red-800 hover:to-red-600 text-white text-lg py-6 rounded-full shadow-xl transition-all transform hover:scale-105 uppercase flex items-center justify-center space-x-2 disabled:opacity-50"
                  aria-busy={isLoading}
                >
                  {isLoading ? (
                    <>
                      <Loader2 className="w-5 h-5 animate-spin" />
                      <span>Processing...</span>
                    </>
                  ) : (
                    <>
                      <PieChart className="w-5 h-5 mr-2" />
                      <span>Analyze Syllabus</span>
                    </>
                  )}
                </Button>
              </form>
            ) : (
              <div className="flex flex-col space-y-6">
                {/* Overall Score Card */}
                <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow-md border border-gray-200 dark:border-gray-700">
                  <div className="flex flex-col md:flex-row items-center justify-between">
                    <div className="mb-4 md:mb-0">
                      <h3 className="text-xl font-bold text-gray-800 dark:text-gray-200 mb-2">
                        Overall Assessment
                      </h3>
                      <p className="text-sm text-gray-600 dark:text-gray-400 mb-2">
                        <span className="font-medium">File Analyzed:</span> {file?.name || "Unknown File"}
                      </p>
                      
                      {backendResponse?.enhancement?.scoring?.score >= 80 ? (
                        <Badge className="bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-200">
                          Excellent
                        </Badge>
                      ) : backendResponse?.enhancement?.scoring?.score >= 60 ? (
                        <Badge className="bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-200">
                          Good - Needs Improvement
                        </Badge>
                      ) : (
                        <Badge className="bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-200">
                          Needs Significant Improvement
                        </Badge>
                      )}
                    </div>
                    
                    <ScoreGauge score={backendResponse?.enhancement?.scoring?.score || 0} />
                  </div>
                  
                  <div className="mt-6">
                    <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">Assessment Breakdown:</h4>
                    <div className="space-y-3">
                      <div>
                        <div className="flex justify-between text-xs mb-1">
                          <span>Bloom Coverage: {backendResponse?.enhancement?.scoring?.bloom_coverage || 0}%</span>
                        </div>
                        <Progress 
                          value={backendResponse?.enhancement?.scoring?.bloom_coverage || 0} 
                          className="h-2"
                          indicatorClassName="bg-green-500"
                        />
                      </div>
                      <div>
                        <div className="flex justify-between text-xs mb-1">
                          <span>Alignment: {backendResponse?.enhancement?.scoring?.alignment_score || 0}%</span>
                        </div>
                        <Progress 
                          value={backendResponse?.enhancement?.scoring?.alignment_score || 0} 
                          className="h-2"
                          indicatorClassName="bg-blue-500" 
                        />
                      </div>
                      <div>
                        <div className="flex justify-between text-xs mb-1">
                          <span>Completeness: {backendResponse?.enhancement?.scoring?.completeness_score || 0}%</span>
                        </div>
                        <Progress 
                          value={backendResponse?.enhancement?.scoring?.completeness_score || 0}
                          className="h-2" 
                          indicatorClassName="bg-pink-500"
                        />
                      </div>
                    </div>
                  </div>
                </div>

                {/* Results Tabs */}
                <Tabs defaultValue="outcomes" className="w-full" onValueChange={setActiveTab}>
                  <TabsList className="grid grid-cols-3 mb-6">
                    <TabsTrigger value="outcomes" className="flex items-center">
                      <Book className="w-4 h-4 mr-2" />
                      <span>Learning Outcomes</span>
                    </TabsTrigger>
                    <TabsTrigger value="analysis" className="flex items-center">
                      <PieChart className="w-4 h-4 mr-2" />
                      <span>Analysis</span>
                    </TabsTrigger>
                    <TabsTrigger value="suggestions" className="flex items-center">
                      <List className="w-4 h-4 mr-2" />
                      <span>Suggestions</span>
                    </TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="outcomes" className="space-y-4">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <Card className="border border-gray-200 dark:border-gray-700 shadow-sm">
                        <CardHeader className="bg-gray-50 dark:bg-gray-800/50 pb-2">
                          <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200 flex items-center">
                            <FileText className="w-4 h-4 mr-2 text-gray-500 dark:text-gray-400" />
                            Original Learning Outcomes
                          </h3>
                        </CardHeader>
                        <CardContent className="p-4">
                          {backendResponse?.syllabus?.content?.objectives?.length === 0 ? (
                            <p className="text-sm text-gray-500 dark:text-gray-400">
                              No objectives provided in the syllabus.
                            </p>
                          ) : (
                            <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300 space-y-2">
                              {backendResponse?.syllabus?.content?.objectives?.map((item, index) => (
                                <li key={index} className="mb-1">{item}</li>
                              ))}
                            </ul>
                          )}
                        </CardContent>
                      </Card>
                      
                      <Card className="border border-red-100 dark:border-red-900/30 shadow-sm bg-red-50/50 dark:bg-red-900/10">
                        <CardHeader className="bg-red-100 dark:bg-red-900/30 pb-2">
                          <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200 flex items-center">
                            <CheckCircle className="w-4 h-4 mr-2 text-red-600 dark:text-red-400" />
                            Improved Learning Outcomes
                          </h3>
                        </CardHeader>
                        <CardContent className="p-4">
                          {backendResponse?.enhancement?.improved_outcomes?.length === 0 ? (
                            <p className="text-sm text-red-500 dark:text-red-400">
                              No improved outcomes generated.
                            </p>
                          ) : (
                            <ul className="list-disc pl-5 text-sm text-gray-700 dark:text-gray-300 space-y-2">
                              {backendResponse?.enhancement?.improved_outcomes?.map((item, index) => (
                                <li key={index} className="mb-1 font-medium">{item}</li>
                              ))}
                            </ul>
                          )}

                          <div className="mt-4 pt-4 border-t border-red-200 dark:border-red-900/30">
                            <p className="text-xs text-red-700 dark:text-red-300 italic flex items-center">
                              <Info className="w-3 h-3 mr-1" />
                              These improved outcomes use Bloom's taxonomy verbs and are more measurable and specific.
                            </p>
                          </div>
                        </CardContent>
                      </Card>
                    </div>
                    
                    <Card className="border border-blue-100 dark:border-blue-900/30 shadow-sm bg-blue-50/50 dark:bg-blue-900/10">
                      <CardHeader className="bg-blue-100 dark:bg-blue-900/30 pb-2 flex flex-row items-center justify-between">
                        <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                          Learning Outcomes Comparison
                        </h3>
                        <TooltipProvider>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Info className="w-4 h-4 text-blue-600 dark:text-blue-400" />
                            </TooltipTrigger>
                            <TooltipContent>
                              <p className="text-sm max-w-xs">
                                Well-written learning outcomes are specific, measurable, and use active verbs from Bloom's taxonomy.
                              </p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>
                      </CardHeader>
                      <CardContent className="p-4">
                        <div className="space-y-4">
                          <p className="text-sm text-gray-700 dark:text-gray-300">
                            The improved learning outcomes focus on making your objectives:
                          </p>
                          <div className="grid grid-cols-1 md:grid-cols-3 gap-3">
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-blue-200 dark:border-blue-900/30">
                              <h4 className="font-medium text-sm mb-1 text-blue-700 dark:text-blue-300">Specific</h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Clearly state what students will know or be able to do
                              </p>
                            </div>
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-blue-200 dark:border-blue-900/30">
                              <h4 className="font-medium text-sm mb-1 text-blue-700 dark:text-blue-300">Measurable</h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Allow for clear assessment of student achievement
                              </p>
                            </div>
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-blue-200 dark:border-blue-900/30">
                              <h4 className="font-medium text-sm mb-1 text-blue-700 dark:text-blue-300">Bloom-aligned</h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Use appropriate cognitive level verbs from Bloom's taxonomy
                              </p>
                            </div>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  </TabsContent>
                  
                  <TabsContent value="analysis" className="space-y-6">
                    <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                      <Card>
                        <CardHeader className="pb-2">
                          <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Bloom's Taxonomy Analysis
                          </h3>
                        </CardHeader>
                        <CardContent className="p-4">
                          <BloomTaxonomyBreakdown bloomAnalysis={backendResponse?.enhancement?.bloom_analysis} />
                          
                          <div className="mt-6 pt-4 border-t border-gray-200 dark:border-gray-700">
                            <h4 className="text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                              Why This Matters:
                            </h4>
                            <p className="text-xs text-gray-600 dark:text-gray-400">
                              A well-designed syllabus should include learning outcomes across multiple cognitive levels 
                              of Bloom's taxonomy to ensure students develop both lower-order thinking skills (remember, understand) 
                              and higher-order thinking skills (analyze, evaluate, create).
                            </p>
                          </div>
                        </CardContent>
                      </Card>
                      
                      <Card>
                        <CardHeader className="pb-2">
                          <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                            Assessment Metrics
                          </h3>
                        </CardHeader>
                        <CardContent className="p-4">
                          {backendResponse?.enhancement?.scoring && (
                            <div className="flex flex-col items-center">
                              <div className="h-64 w-full">
                                <ResponsiveContainer width="100%" height="100%">
                                  <RechartsChart>
                                    <Pie
                                      data={generateScoreData(backendResponse?.enhancement?.scoring)}
                                      cx="50%"
                                      cy="50%"
                                      innerRadius={60}
                                      outerRadius={80}
                                      paddingAngle={5}
                                      dataKey="value"
                                      label={({ name, value }) => `${name}: ${value}%`}
                                    >
                                      {generateScoreData(backendResponse?.enhancement?.scoring).map((entry, index) => (
                                        <Cell key={`cell-${index}`} fill={entry.color} />
                                      ))}
                                    </Pie>
                                    <Legend verticalAlign="bottom" height={36} />
                                    <ChartTooltip />
                                  </RechartsChart>
                                </ResponsiveContainer>
                              </div>
                              
                              <div className="mt-4 w-full">
                                <div className="text-center mb-2">
                                  <span className="text-sm font-medium text-gray-700 dark:text-gray-300">
                                    Overall Score: 
                                    <span className="ml-1 text-lg font-bold text-red-600 dark:text-red-400">
                                      {backendResponse?.enhancement?.scoring?.score || 0}/100
                                    </span>
                                  </span>
                                </div>
                              </div>
                            </div>
                          )}
                        </CardContent>
                      </Card>
                    </div>
                    
                    <Card>
                      <CardHeader className="pb-2">
                        <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                          Misalignments
                        </h3>
                      </CardHeader>
                      <CardContent className="p-4">
                        {backendResponse?.enhancement?.misalignments?.length === 0 ? (
                          <div className="bg-green-50 dark:bg-green-900/20 p-4 rounded-lg border border-green-200 dark:border-green-800">
                            <div className="flex">
                              <div className="flex-shrink-0">
                                <CheckCircle className="h-5 w-5 text-green-500" />
                              </div>
                              <div className="ml-3">
                                <p className="text-sm text-green-700 dark:text-green-300">
                                  No misalignments detected. Your learning outcomes are well-aligned with assessments.
                                </p>
                              </div>
                            </div>
                          </div>
                        ) : (
                          <div className="space-y-4">
                            <p className="text-sm text-gray-700 dark:text-gray-300">
                              The following misalignments were detected between your learning outcomes and assessments:
                            </p>
                            <div className="bg-yellow-50 dark:bg-yellow-900/20 p-4 rounded-lg border border-yellow-200 dark:border-yellow-800">
                              <ul className="list-disc pl-5 text-sm text-yellow-800 dark:text-yellow-200 space-y-2">
                                {backendResponse?.enhancement?.misalignments?.map((item, index) => (
                                  <li key={index} className="mb-1">{item}</li>
                                ))}
                              </ul>
                            </div>
                            <p className="text-xs text-gray-600 dark:text-gray-400 italic">
                              Alignment ensures that what you teach (learning outcomes) matches how you assess student learning.
                            </p>
                          </div>
                        )}
                      </CardContent>
                    </Card>
                  </TabsContent>
                  
                  <TabsContent value="suggestions" className="space-y-4">
                    <Card>
                      <CardHeader className="pb-2">
                        <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200">
                          Improvement Suggestions
                        </h3>
                      </CardHeader>
                      <CardContent className="p-4">
                        {backendResponse?.enhancement?.suggestions?.length === 0 ? (
                          <p className="text-sm text-gray-500 dark:text-gray-400">
                            No suggestions provided.
                          </p>
                        ) : (
                          <div className="space-y-3">
                            {backendResponse?.enhancement?.suggestions?.map((item, index) => (
                              <div 
                                key={index} 
                                className="bg-white dark:bg-gray-800 p-4 rounded-lg border-l-4 border-blue-500 dark:border-blue-400 shadow-sm"
                              >
                                <p className="text-sm text-gray-700 dark:text-gray-300">{item}</p>
                              </div>
                            ))}
                          </div>
                        )}
                      </CardContent>
                    </Card>
                    
                    <Card className="border border-green-100 dark:border-green-900/30 bg-green-50/50 dark:bg-green-900/10">
                      <CardHeader className="pb-2 bg-green-100 dark:bg-green-900/30">
                        <h3 className="text-md font-semibold text-gray-800 dark:text-gray-200 flex items-center">
                          <Book className="w-4 h-4 mr-2 text-green-600 dark:text-green-400" />
                          Best Practices
                        </h3>
                      </CardHeader>
                      <CardContent className="p-4">
                        <div className="space-y-4">
                          <p className="text-sm text-gray-700 dark:text-gray-300">
                            Consider these best practices when revising your syllabus:
                          </p>
                          <div className="grid grid-cols-1 md:grid-cols-2 gap-3">
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-green-200 dark:border-green-800">
                              <h4 className="font-medium text-sm mb-1 text-green-700 dark:text-green-300">
                                Use Action Verbs
                              </h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Begin learning outcomes with observable, measurable verbs (analyze, evaluate, create).
                              </p>
                            </div>
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-green-200 dark:border-green-800">
                              <h4 className="font-medium text-sm mb-1 text-green-700 dark:text-green-300">
                                Balance Cognitive Levels
                              </h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Include outcomes across multiple levels of Bloom's taxonomy.
                              </p>
                            </div>
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-green-200 dark:border-green-800">
                              <h4 className="font-medium text-sm mb-1 text-green-700 dark:text-green-300">
                                Ensure Measurability
                              </h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Make sure each outcome can be assessed through course activities.
                              </p>
                            </div>
                            <div className="bg-white dark:bg-gray-800 p-3 rounded-lg border border-green-200 dark:border-green-800">
                              <h4 className="font-medium text-sm mb-1 text-green-700 dark:text-green-300">
                                Align With Assessments
                              </h4>
                              <p className="text-xs text-gray-600 dark:text-gray-400">
                                Create clear connections between outcomes and assessment methods.
                              </p>
                            </div>
                          </div>
                        </div>
                      </CardContent>
                    </Card>
                  </TabsContent>
                </Tabs>

                <div className="flex flex-col md:flex-row space-y-4 md:space-y-0 md:space-x-4">
                  <Button
                    onClick={handleDownload}
                    className="bg-green-600 hover:bg-green-700 text-white text-lg py-4 rounded-xl shadow-md flex items-center justify-center space-x-2 flex-1"
                    aria-label="Download report as PDF"
                  >
                    <Download className="w-5 h-5" />
                    <span>Download PDF Report</span>
                  </Button>

                  <Button
                    onClick={handleReset}
                    variant="outline"
                    className="border-red-500 text-red-500 hover:bg-red-100 dark:hover:bg-red-900 py-4 rounded-xl shadow-md flex items-center justify-center space-x-2 flex-1"
                    aria-label="Reset form"
                  >
                    <RotateCw className="w-5 h-5" />
                    <span>Process Another Syllabus</span>
                  </Button>
                </div>
              </div>
            )}
          </CardContent>
        </Card>
        
        <div className="text-center text-gray-400 text-sm">
           {new Date().getFullYear()} | Syllabus Enhancement Tool | Powered by AI
        </div>
      </div>
    </div>
  );
};

export default DashboardPage;